<template>
    <div>
        <div class="container">
            <div class="form-box">
                <el-form ref="form" :model="DrawSaveSet" label-width="200px">
                    <el-form-item label="注册赠送类型">
                        <el-select v-model.number="DrawSaveSet.RegGiveType" placeholder="请选择">
                            <el-option v-for="item in RegGiveTypes" :label="item.name" :key="item.value" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="注册赠送数量">
                        <el-input v-model="DrawSaveSet.RegGiveCount"></el-input>
                    </el-form-item>


                    <el-form-item label="充值提示">
                        <el-input v-model="DrawSaveSet.DrawHint"></el-input>
                    </el-form-item>
                    <el-form-item label="最小充值">
                        <el-input-number class="loose-input" v-model="DrawSaveSet.MinSave" :step="1" :min="0" ></el-input-number>
                    </el-form-item>
                    <el-form-item label="充值赠送类型">
                        <el-select v-model.number="DrawSaveSet.SaveGiveType" placeholder="请选择">
                            <el-option v-for="item in SaveGiveTypes" :label="item.name" :key="item.value" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="充值赠送数量比">
                        <el-input-number class="loose-input" v-model="DrawSaveSet.SaveGiveRatio" :precision="3" :step="0.01" :min="0" ></el-input-number>
                    </el-form-item>
                    <el-form-item label="提现提示">
                        <el-input v-model="DrawSaveSet.SaveHint"></el-input>
                    </el-form-item>
                    <el-form-item label="最小提现">
                        <el-input-number class="loose-input" v-model="DrawSaveSet.MinDraw" :step="1" :min="0" ></el-input-number>
                    </el-form-item>
                    <el-form-item label="提现的开始时间（小时）">
                        <el-input-number class="loose-input" v-model="DrawSaveSet.DrawBeginHr" :step="1" :max="23" :min="0" ></el-input-number>
                    </el-form-item>
                    <el-form-item label="提现的工作（小时）">
                        <el-input-number class="loose-input" v-model="DrawSaveSet.DrawWorkHr" :step="1" :max="24" :min="0" ></el-input-number>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">表单提交</el-button>
                        <el-button>取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>
<script>
    import { request } from '../../utils/http';
    import Tinymce from '../../components/Tinymce'

    export default {
        name: 'DrawSaveSet',
        components: {
            Tinymce
        },
        data() {
            return {
                DrawSaveSet:{},
                query: {
                    id:0
                },
                content: '',
                editorOption: {
                    placeholder: 'Hello World'
                },
                RegGiveTypes:[
                    {name:'金币',value:1},
                    {name:'积分',value:2},
                    {name:'转盘',value:3},
                ],
                SaveGiveTypes:[
                    {name:'金币',value:1},
                    {name:'积分',value:2},
                    {name:'转盘',value:3},
                ],

            };
        },
        methods: {
            onSubmit() {
                console.log(this.DrawSaveSet);
                request({url: 'filmbll/updatedrawsaveset', method:'post', data:this.DrawSaveSet }).then((res)=>{
                    if (res.code==200){
                        this.$message.success('提交成功！');
                    }
                });
            } ,
            getData(id) {
                this.query.id = id;
                request({ url:'filmbll/getdrawsaveset', method:'post',data: this.query }).then((res)=>{
                    if (res.code==200){
                        console.log(res.obj)
                        this.DrawSaveSet = res.obj;
                        console.log(this.DrawSaveSet);
                    }
                });
            },
        },
        created() {
            // console.log(this.$route.query);
            this.getData(11);
        },mounted() {
            // console.log(this.$route.query);
        }
    };
</script>

<style scoped>

</style>